.ft-game-open-btn.active:hover {
  pointer-events: none;
}


@keyframes shake {
  10%, 90% {

    transform: translate3d(-1px, 0, 0);
  }
  
  20%, 80% {
    transform: translate3d(3px, 0, 0);
  }

  30%, 50%, 70% {
    transform: translate3d(-2px, 0, 0);
  }

  40%, 60% {
    transform: translate3d(3px, 0, 0);
  }
}


.site-overlay {
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background: #000000;
  opacity: 0;
  transition: opacity 0.75s;
}

.site-overlay.active {
  transition: opacity 0.75s;
  opacity: 0.75;
  z-index: 10;
}

.end-screen-wrapper {
  z-index: 1000;
  display: flex;
  min-width: 100%;
  position: absolute;
  min-height: 100%;
  flex-direction: column;
  font-family: "Thirtytwobit";
}

@media(min-width: 1024px) {
.end-screen-wrapper {
  overflow: hidden;
  max-height: 100vh;
}
}

.overlay {
  position: absolute;
  left: 0;
  width: 100%;
  pointer-events: none;
  height: 100%;
  pointer-events: all;
  background: #000000;
  opacity: 0.75;
  z-index: -1;
}
.end-screen {
