}

.eightbit-btn {
  background: #92cd41;
  display: inline-block;
  position: relative !important;
  text-align: center;
  font-size: 45px;
  padding: 10px 20px 15px;
  cursor: pointer;
  font-family: "Thirtytwobit";
  text-decoration: none;
  color: white;
  box-shadow: inset -4px -4px 0px 0px #4aa52e;
  overflow: visible !important;
}
.eightbit-btn:hover,
.eightbit-btn:focus {
  background: #76c442;
  box-shadow: inset -6px -6px 0px 0px #4aa52e;
}
.eightbit-btn:active {
  box-shadow: inset 4px 4px 0px 0px #4aa52e;
}
.eightbit-btn:before,
.eightbit-btn:after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  box-sizing: content-box;
}
.eightbit-btn:before {
  top: -6px;
  left: 0;
  border-top: 6px black solid;
  border-bottom: 6px black solid;
}
.eightbit-btn:after {
  left: -6px;
  top: 0;
  border-left: 6px black solid;
  border-right: 6px black solid;
}

.eightbit-btn canvas {
  width: 100%;
}
.text-wrap {
  display: flex;
  width: 75%;
  z-index: 1000;
  height: 100%;
  padding-top: 100px;
  padding-bottom: 100px;
}
p.story {
  font-size: 45px;
  opacity: 0;
  clip-path: inset(0px 0% 0px 100%);
  padding-bottom: 45px;
  font-family: "thirtytwobit";
  line-height: normal;
}

p.story.reveal {
  opacity: 1;
  clip-path: inset(0px 0% 0px 1px);
  transition: clip-path 1.5s cubic-bezier(0, 0.64, 0.27, 1.14);
  font-family: "thirtytwobit";
  line-height: normal;
}

#app {
  min-height: 120px;
}
